.navbar{
    display: flex;
    padding: .5rem 0;
    @include rwd($rwd-md){
        padding: 1.25rem 0;
    }
    @include rwd($rwd-lg){
        padding: 1.75rem 0;
    }
    a{
        color: $white;
        display: block;
        padding: 0 2rem;
        &:first-child {
            padding-left: 0;
        }
        &:hover{
            color: darken($white, 15%);
        }
    }
}