// font-size
@each $name, $font-size in $font-sizes{
    .font-#{$name}{
        font-size: $font-size;
    }
}
@each $breakpoint-name, $breakpoint in $grid-breakpoints{
    @each $name, $font-size in $font-sizes{
        @include rwd($breakpoint){
            .font-#{$breakpoint-name}-#{$name}{
                font-size: $font-size;
            }
        }
    }
}

// text color
@each $name, $color in $colors{
    .text-#{$name}{
        color: $color;
    }
}